html {
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
  font-size: 16px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #24292f;
  width: 100%;
  height: 100vh;
}

.hidden {
  display: none !important;
}

.container {
  width: 100%;
  margin: 0 1rem 0 1rem;
}

.dashboard {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90vh;
  max-width: 1400px;
  background-color: #24292f;
  border: 3px solid #1480fc;
  border-radius: 25px;
  margin: 0 1rem 0 1rem;
  -webkit-box-shadow: 0px 0px 38px -5px rgb(20, 128, 252);
  -moz-box-shadow: 0px 0px 38px -5px rgb(20, 128, 252);
  box-shadow: 0px 0px 38px -5px rgb(20, 128, 252);
  position: relative;
}
.dashboard__panel {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70vh;
}
.dashboard__playerName {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 1rem;
  color: white;
}
.dashboard__history {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 1rem;
  border: 2px solid #000000;
}
.dashboard__exit {
  position: absolute;
  left: 20px;
  bottom: 20px;
  width: 42px;
  cursor: pointer;
  transition: all 200ms;
}
.dashboard__exit:hover {
  transform: scale(1.1);
}
.dashboard__instruction {
  position: absolute;
  right: 20px;
  bottom: 10px;
  text-align: center;
}
.dashboard__instruction p {
  width: 6rem;
  color: white;
  font-size: 0.625rem;
  line-height: 1rem;
  font-weight: 300;
}
.dashboard__historyPanel {
  position: absolute;
  top: 70px;
  left: 20px;
  width: 250px;
  height: 200px;
  background: rgba(191, 191, 191, 0.36);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10.6px);
  -webkit-backdrop-filter: blur(10.6px);
  border: 1px solid rgba(191, 191, 191, 0.78);
  padding: 1.25rem 1.75rem 1.25rem 1.75rem;
  font-weight: 500;
  line-height: 1.25rem;
  overflow-y: auto;
  color: white;
}
.dashboard__historyPanel--close {
  position: absolute;
  left: 0;
  top: 0;
  cursor: pointer;
  transition: all 200ms;
}
.dashboard__historyPanel--close:hover {
  transform: scale(1.1);
}
.dashboard__data {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgba(191, 191, 191, 0.36);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10.6px);
  -webkit-backdrop-filter: blur(10.6px);
  border: 1px solid rgba(191, 191, 191, 0.78);
  width: 90%;
  height: 90%;
  border-radius: 25px;
}
.dashboard__data--title {
  font-size: 2.5rem;
  font-weight: bold;
  background: -webkit-linear-gradient(#1480fc, #000000);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.dashboard__data--btn {
  width: 23.125rem;
  margin-top: 0.75rem;
}
.dashboard__data--input {
  margin: 0.25rem 0 0.25rem 0;
}
.dashboard__data--label {
  font-size: 1.25rem;
  margin: 0.25rem 0 0.25rem 0;
}

.error-input {
  border: 2px solid red !important;
}

.errors {
  color: red;
  margin-top: 1rem;
}

.board {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 90%;
}

.cell {
  border: 1.5px solid grey;
  border-style: inset;
  width: 3rem;
  height: 3rem;
  background-color: #fff;
}

.whiteCell {
  background-color: white;
}

.hero {
  background-image: url("/src/images/octopus.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.btn {
  padding: 0.75rem 2rem 0.75rem 2rem;
  background-color: #1480fc;
  border: 2px solid #000000;
  border-radius: 1.25rem;
  cursor: pointer;
  transition: all 200ms;
}
.btn:hover {
  transform: scale(1.1);
}

.input {
  width: 100%;
  max-width: 23.125rem;
  height: 40px;
  border: 2px solid #000000;
  border-radius: 1rem;
  padding: 0.25rem;
  transition: all 200ms;
}
.input:focus {
  border-color: #1480fc;
  -webkit-box-shadow: 0px 0px 30px -5px rgb(20, 128, 252);
  -moz-box-shadow: 0px 0px 30px -5px rgb(20, 128, 252);
  box-shadow: 0px 0px 30px -5px rgb(20, 128, 252);
}